body {
  height: 100%;
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  outline: none;
}

body {
  position: relative;
  min-width: 25rem;
  margin: 0;
  padding: 2rem;
}

.cards__title {
  font-size: 1.3rem;
  color: #306d30;
}

#check-container {
  display: inline-block;
  padding: 0;
  gap: 0.5rem;
}

.card_container {
  display: flex;
  width: 100%;
  justify-content: start;
}

.card_img_container {
  width: 39px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 10px;
  position: relative;
  border-radius: 3px;
  overflow: hidden;
}

.ae {
  background-image: url("https://avatars.mds.yandex.net/i?id=e24f1ee8232af3b75649adedd1843143d59d5c12-9678040-images-thumbs&n=13");
}

.dc {
  background-image: url("https://avatars.mds.yandex.net/i?id=f951e1828b5b5713761caa13f7170b6c-5221159-images-thumbs&n=13");
}

.disc {
  background-image: url("https://avatars.mds.yandex.net/i?id=a65cfdfc686817f9ec4ba3dbbc15ae000d3c0d16-9217411-images-thumbs&n=13");
}

.jcb {
  background-image: url("https://avatars.mds.yandex.net/i?id=9162f877b88f36a72217ad45f772d29956d2a0da-8226521-images-thumbs&n=13");
}

.world {
  background-image: url("https://avatars.mds.yandex.net/i?id=4559f50a1aaaf3a2a204bf63a6d1273fc4795de6-8076535-images-thumbs&n=13");
}

.mc {
  background-image: url("https://avatars.mds.yandex.net/i?id=5f62449702d30ccff2bc14dc7203ee409e56a042-9069926-images-thumbs&n=13");
}

.visa {
  background-image: url("https://avatars.mds.yandex.net/i?id=82d964b54212c25ad1b47aca3c68233e7a2b8bb2-5233806-images-thumbs&n=13");
}

.maestro {
  background-image: url("https://avatars.mds.yandex.net/i?id=b0013248d80ffe9567bbbb1016e29b50a931dbf0-9066147-images-thumbs&n=13");
}

.mask {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: gray;
  z-index: 9;
}
.form {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 20rem;
  gap: 0.5rem;
}

.input {
  height: 2rem;
  padding: 0.5rem;
  border: 0.1rem solid #306d30;
  border-radius: 0.3rem;
}

.button {
  max-width: 10rem;
  height: 1.5rem;
  border: 0.1rem solid #306d30;
  border-radius: 0.3rem;
  color: #ffffff;
  background-color: #5cb85c;
  cursor: pointer;
}

.error {
  color: red;
  display: none;
}

.none {
  display: none;
}

.block {
  display: block;
}

.op_09 {
  opacity: 0.6;
}

.no_op {
  opacity: 0;
}
